    'st-top-brand-3': 'Top Clicked Brands #3',
    'st-top-category-1': 'Top Clicked Category #1',
    'st-top-category-2': 'Top Clicked Category #2',
    'st-top-category-3': 'Top Clicked Category #3',
    'st-top-asin-1': 'Top Clicked Product #1: ASIN',
    'st-top-asin-title-1': 'Top Clicked Product #1: Product Title',
    'st-top-asin-click-share-1': 'Top Clicked Product #1: Click Share',
    'st-top-asin-conversion-share-1': 'Top Clicked Product #1: Conversion Share',
    'st-top-asin-2': 'Top Clicked Product #2: ASIN',
    'st-top-asin-title-2': 'Top Clicked Product #2: Product Title',
