import type { BusinessReportsRequestType } from '@seller-utilities/database';
import { gql } from 'graphql-request';

import GraphQLHelper from '../../../GraphQLHelper';
import type { AmazonDateRange } from '../../InlinePageRequest/AdvRequest';

export default class SalesAndTrafficRequestUtils {
  private static readonly getLegacyReportId = (requestType: BusinessReportsRequestType) => {
    switch (requestType) {
      case 'Date':
        return '102:SalesTrafficTimeSeries';
      case 'SKU':
        return '102:DetailSalesTrafficBySKU';
      case 'ChildASIN':
        return '102:DetailSalesTrafficByChildItem';
      case 'ParentASIN':
        return '102:DetailSalesTrafficByParentItem';
      default:
        throw new Error(`Unknown request type: ${requestType}`);
    }
  };

  static readonly SELLER_METADATA_REQUEST_BODY = GraphQLHelper.createRequestBody(
    gql`
      query sellerMetadata {
        getSellerMetaData {
          marketplaceId
          sellerId
          asinUrl
          byDateReports {
            reportDefinitionId
            title
            translationKey
            __typename
          }
          byAsinReports {
            reportDefinitionId
            title
            translationKey
            __typename
          }
          otherReports {
            reportDefinitionId
            title
            translationKey
            __typename
          }
          localeInfo {
            currencyCode
            locale
            timeZone
            __typename
          }
          __typename
        }
      }
    `,
    {
      input: {}
    },
    'sellerMetadata'
  );

  static getSalesAndTrafficReportDataQuery = (amazonDateRange: AmazonDateRange) =>
    GraphQLHelper.createRequestBody(
      gql`
        query reportDataQuery($input: GetReportDataInput) {
          getReportData(input: $input) {
            granularity
            hadPrevious
            hasNext
            size
            startDate
            endDate
            page
            columns {
              label
              valueFormat
              isGraphable
              translationKey
              isDefaultSortAscending
              isDefaultGraphed
              isDefaultSelected
              isDefaultSortColumn
              __typename
            }
            rows
            __typename
          }
        }
      `,
      {
        input: {
          endDate: amazonDateRange.endISO,
          granularity: amazonDateRange.granularity,
          legacyReportId: this.getLegacyReportId('Date'),
          startDate: amazonDateRange.startISO
        }
      },
      'reportDataQuery'
    );

  static getSalesAndTrafficByASINReportDataQuery = (
    amazonDateRange: AmazonDateRange,
    requestType: BusinessReportsRequestType,
    asins: Array<string>,
    page: number
  ) => {
    if (requestType === 'Date') {
      return this.getSalesAndTrafficReportDataQuery(amazonDateRange);
    }

    const legacyReportId = this.getLegacyReportId(requestType);
    return GraphQLHelper.createRequestBody(
      gql`
        query reportDataQuery($input: GetReportDataInput) {
          getReportData(input: $input) {
            granularity
            hadPrevious
            hasNext
            size
            startDate
            endDate
            page
            columns {
              label
              valueFormat
              isGraphable
              translationKey
              isDefaultSortAscending
              isDefaultGraphed
              isDefaultSelected
              isDefaultSortColumn
              __typename
            }
            rows
            __typename
          }
        }
      `,
      {
        input: {
          asins,
          endDate: amazonDateRange.endISO,
          legacyReportId,
          page,
          startDate: amazonDateRange.startISO
        }
      },
      'reportDataQuery'
    );
  };

  static getReportColumnsQuery = (requestType: BusinessReportsRequestType) => {
    const legacyReportId = this.getLegacyReportId(requestType);
    return GraphQLHelper.createRequestBody(
      gql`
        query reportDataQuery($input: GetReportDataInput) {
          getReportData(input: $input) {
            columns {
              label
              valueFormat
              translationKey
            }
          }
        }
      `,
      { input: { legacyReportId } },
      'reportDataQuery'
    );
  };
}
