import { pgEnum } from 'drizzle-orm/pg-core';

const getEnumData = <U extends string, T extends Readonly<[U, ...Array<U>]>>(enumName: string, enumValues: T) => {
  const drizzleEnum = pgEnum(enumName, enumValues);

  return {
    drizzleEnum,
    enumObject: enumValues.reduce((acc, cur) => ({ ...acc, [cur]: cur }), {} as { [K in T[number]]: K })
  } as const;
};

export const { drizzleEnum: ReportDataTypeEnum, enumObject: ReportDataType } = getEnumData('ReportDataType', [
  'SQPASINs',
  'SQPBrands',
  'SCP',
  'TST',
  'BusinessByDate',
  'BusinessByASINSKU',
  'BusinessByASINChild',
  'BusinessByASINParent'
]);
export type ReportDataType = keyof typeof ReportDataType;

export const { drizzleEnum: BusinessReportsRequestTypeEnum, enumObject: BusinessReportsRequestType } = getEnumData(
  'BusinessReportsRequestType',
  ['Date', 'SKU', 'ChildASIN', 'ParentASIN']
);
export type BusinessReportsRequestType = keyof typeof BusinessReportsRequestType;

export const {
  drizzleEnum: GoogleDriveDataConfigurationDestinationTypeEnum,
  enumObject: GoogleDriveDataConfigurationDestinationType
} = getEnumData('GoogleDriveDataConfigurationDestinationType', ['Folder', 'Sheet']);
export type GoogleDriveDataConfigurationDestinationType = keyof typeof GoogleDriveDataConfigurationDestinationType;

export const { drizzleEnum: ThemeModeEnum, enumObject: ThemeMode } = getEnumData('ThemeMode', ['light', 'dark']);
export type ThemeMode = keyof typeof ThemeMode;
