import { boolean, integer, jsonb, pgTable, text, timestamp, uniqueIndex, uuid } from 'drizzle-orm/pg-core';

import type { GoogleSheet, GoogleSheetColumnMappings, SellerCentralMarketplace, UIEnhancementSettings } from '../types';
import {
  BusinessReportsRequestTypeEnum,
  GoogleDriveDataConfigurationDestinationTypeEnum,
  ReportDataTypeEnum,
  ThemeModeEnum
} from './enums';

export const users = pgTable('User', {
  email: text('email').unique().notNull(),
  firstName: text('firstName').notNull(),
  hasAccess: boolean('hasAccess').default(true).notNull(),
  id: uuid('id').primaryKey().defaultRandom(),
  joinedDate: timestamp('joinedDate', { mode: 'string', precision: 3, withTimezone: true }).defaultNow().notNull(),
  lastLoginDate: timestamp('lastLoginDate', { mode: 'string', precision: 3, withTimezone: true })
    .defaultNow()
    .notNull(),
  lastName: text('lastName').notNull(),
  providerAccountId: text('providerAccountId').notNull()
});

export const googleDriveConnections = pgTable('GoogleDriveConnection', {
  id: uuid('id').primaryKey().defaultRandom(),
  refreshToken: text('refreshToken').notNull(),
  userId: uuid('userId')
    .references(() => users.id, { onDelete: 'cascade', onUpdate: 'cascade' })
    .unique()
    .notNull()
});

export const googleDriveDataConfigurations = pgTable(
  'GoogleDriveDataConfiguration',
  {
    id: uuid('id').primaryKey().defaultRandom(),
    columnMappings: jsonb('columnMappings').$type<GoogleSheetColumnMappings>(),
    sheet: jsonb('sheet').$type<GoogleSheet>(),
    destinationType: GoogleDriveDataConfigurationDestinationTypeEnum('destinationType').notNull(),
    googleDriveItemId: text('googleDriveItemId').notNull(),
    headerRowsToSkip: integer('headerRowsToSkip'),
    lastUsedDate: timestamp('lastUsedDate', { mode: 'string', precision: 3, withTimezone: true }),
    name: text('name').unique().notNull(),
    overwriteRows: boolean('overwriteRows'),
    reportType: ReportDataTypeEnum('reportType').notNull(),
    userId: uuid('userId')
      .references(() => users.id, { onDelete: 'cascade', onUpdate: 'cascade' })
      .notNull(),
    googleDriveConnectionId: uuid('googleDriveConnectionId')
      .references(() => googleDriveConnections.id, { onDelete: 'cascade', onUpdate: 'cascade' })
      .notNull()
  },
  (table) => [uniqueIndex().on(table.userId, table.name)]
);

export const businessReportsColumnSelections = pgTable(
  'BusinessReportsColumnSelection',
  {
    id: uuid('id').primaryKey().defaultRandom(),
    selections: jsonb('selections').$type<Record<string, number>>().notNull(),
    requestType: BusinessReportsRequestTypeEnum('requestType').notNull(),
    userId: uuid('userId')
      .references(() => users.id, { onDelete: 'cascade', onUpdate: 'cascade' })
      .notNull(),
    comparisonColumns: jsonb('comparisonColumns').$type<Record<string, number>>()
  },
  (table) => [uniqueIndex().on(table.userId, table.requestType)]
);

export const userSettings = pgTable('UserSettings', {
  activeMarketplace: text('activeMarketplace')
    .default('us' satisfies SellerCentralMarketplace)
    .$type<SellerCentralMarketplace>()
    .notNull(),
  id: uuid('id').primaryKey().defaultRandom(),
  themeMode: ThemeModeEnum('themeMode').default('dark').notNull(),
  uiEnhancements: jsonb('uiEnhancements').$type<UIEnhancementSettings>().notNull(),
  userId: uuid('userId')
    .unique()
    .notNull()
    .references(() => users.id, { onDelete: 'cascade', onUpdate: 'cascade' })
});

export * from './enums';
