// eslint-disable-next-line no-restricted-syntax
export enum ApiError {
  // The data the user provided is incorrect
  BadRequest = 400,
  // The user is not authorized at all
  Unauthorized = 401,
  // Payment processing failed
  PaymentRequired = 402,
  // The user is authorized but not allowed to access the resource
  Forbidden = 403,
  // The resource was not found
  NotFound = 404,
  // The inputs to the resource were not acceptable
  NotAcceptable = 406,
  // There is a conflict in the current state of the resource
  Conflict = 409,
  // The resource is gone
  Gone = 410,
  // The "I'm a teapot" HTTP status code that we use for the sake of handled server errors
  HandledServerError = 418,
  // The request contains invalid data
  UnprocessableEntity = 422,
  // Too many requests in a given amount of time
  TooManyRequests = 429,
  // An error occurred on the server
  ServerError = 500
}

export interface ErrorResponseOptions {
  message?: string;
  headers?: Record<string, unknown> | undefined;
  rawCode?: string | undefined;
  rawData?: any | undefined;
  [key: string]: unknown;
}

export class ErrorResponse extends Error {
  statusCode: number;

  readonly rawCode: ErrorResponseOptions['rawCode'];

  override readonly stack?: string | undefined;

  readonly rawData: ErrorResponseOptions['rawData'];

  readonly data: Record<string, unknown>;

  readonly headers: ErrorResponseOptions['headers'];

  constructor(apiError: ApiError, { headers, message, rawCode, rawData, ...data }: ErrorResponseOptions) {
    super(message);

    this.rawCode = rawCode;
    this.rawData = rawData;
    this.message = message ?? '';
    this.statusCode = apiError;
    this.data = data;
    this.headers = headers;
  }
}
