import type { SellerCentralMarketplace } from '@seller-utilities/database';

import SellerCentralRequest from './SellerCentralRequest';

export default class SellerCentralRequestManager {
  private static marketplaceCache: Partial<Record<SellerCentralMarketplace, SellerCentralRequest>> = {};

  private static getInstance(marketplace: SellerCentralMarketplace) {
    if (SellerCentralRequestManager.marketplaceCache[marketplace]) {
      return SellerCentralRequestManager.marketplaceCache[marketplace];
    }
    const request = new SellerCentralRequest(marketplace);
    SellerCentralRequestManager.marketplaceCache[marketplace] = request;
    return request;
  }

  static async fetchSQPAPIData(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSQPAPIData']>
  ) {
    return this.getInstance(marketplace).fetchSQPAPIData(...params);
  }

  static async fetchSCPAPIData(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSCPAPIData']>
  ) {
    return this.getInstance(marketplace).fetchSCPAPIData(...params);
  }

  static async fetchTSTAPIData(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchTSTAPIData']>
  ) {
    return this.getInstance(marketplace).fetchTSTAPIData(...params);
  }

  static async fetchSQPMetadata(marketplace: SellerCentralMarketplace) {
    return this.getInstance(marketplace).fetchSQPMetadata(marketplace);
  }

  static async fetchSCPMetadata(marketplace: SellerCentralMarketplace) {
    return this.getInstance(marketplace).fetchSCPMetadata(marketplace);
  }

  static async fetchTSTMetadata(marketplace: SellerCentralMarketplace) {
    return this.getInstance(marketplace).fetchTSTMetadata(marketplace);
  }

  static async fetchSalesAndTrafficReportByASIN(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSalesAndTrafficReportByASIN']>
  ) {
    return this.getInstance(marketplace).fetchSalesAndTrafficReportByASIN(...params);
  }

  static async fetchSalesAndTrafficReport(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSalesAndTrafficReport']>
  ) {
    return this.getInstance(marketplace).fetchSalesAndTrafficReport(...params);
  }

  static async fetchSalesAndTrafficReportColumns(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSalesAndTrafficReportColumns']>
  ) {
    return this.getInstance(marketplace).fetchSalesAndTrafficReportColumns(...params);
  }

  static async fetchSellerMetadata(
    marketplace: SellerCentralMarketplace,
    ...params: Parameters<(typeof SellerCentralRequest.prototype)['fetchSellerMetadata']>
  ) {
    return this.getInstance(marketplace).fetchSellerMetadata(...params);
  }

  static async getBaseUrl(marketplace: SellerCentralMarketplace) {
    return this.getInstance(marketplace).baseUrl;
  }
}
