import type { BusinessReportsRequestType, SellerCentralMarketplace } from '@seller-utilities/database';
import { SUPPORTED_MARKETPLACES } from '@seller-utilities/database';
import type {
  BusinessReportsMetadataResponse,
  RawReportMetadataResponse,
  SalesAndTrafficReportResponse,
  SCPAPIDataResponse,
  TSTAPIDataResponse
} from '@seller-utilities/types';
import type { SQPAPIDataResponse } from '@seller-utilities/types/src/api/reports/sqp';

import ReportRequestUtils from '../../reports/ReportRequestUtils';
import type { AmazonDateRange } from '../InlinePageRequest/AdvRequest';
import BaseRequest from './BaseRequest';
import SalesAndTrafficRequestUtils from './utils/SalesAndTrafficRequestUtils';
import type { SCPPayloadParameter } from './utils/SCPRequestUtils';
import SCPRequestUtils from './utils/SCPRequestUtils';
import type { SQPPayloadParameter } from './utils/SQPRequestUtils';
import type { TSTPayloadParameter } from './utils/TSTRequestUtils';
import TSTRequestUtils from './utils/TSTRequestUtils';

export default class SellerCentralRequest extends BaseRequest {
  constructor(marketplace: SellerCentralMarketplace) {
    const baseUrl = `https://sellercentral.amazon.${SUPPORTED_MARKETPLACES[marketplace].urlSuffix}`;
    super(baseUrl, `${baseUrl}/brand-analytics/dashboard/`); // Arbitrary API route to get CSRF token from
  }

  async fetchSQPAPIData(payload: SQPPayloadParameter, isBrands: boolean) {
    await this.addHeadersOverride({
      csrfToken: await this.getCsrfToken(),
      origin: this.baseUrl,
      referer: ReportRequestUtils.generateReportRefererUrl(
        `${this.baseUrl}/brand-analytics/dashboard/query-performance`,
        payload,
        isBrands ? 'query-performance-brands-view' : 'query-performance-asin-view'
      ),
      urlFilter: `${this.baseUrl}/api`
    });

    return (
      await this.makeRESTRequest<SQPAPIDataResponse>(
        '/api/brand-analytics/v1/dashboard/query-performance/reports',
        payload
      )
    ).data.reportsV2[0];
  }

  async fetchSCPAPIData(payload: SCPPayloadParameter) {
    await this.addHeadersOverride({
      csrfToken: await this.getCsrfToken(),
      origin: this.baseUrl,
      referer: SCPRequestUtils.getSCPFetchAPIDataReferer(this.baseUrl, payload),
      urlFilter: `${this.baseUrl}/api`
    });

    return (
      await this.makeRESTRequest<SCPAPIDataResponse>(
        '/api/brand-analytics/v1/dashboard/brand-catalog-performance/reports',
        payload
      )
    ).data.reportsV2[0];
  }

  async fetchTSTAPIData(payload: TSTPayloadParameter) {
    await this.addHeadersOverride({
      csrfToken: await this.getCsrfToken(),
      origin: this.baseUrl,
      referer: TSTRequestUtils.getTSTFetchAPIDataReferer(this.baseUrl, payload),
      urlFilter: `${this.baseUrl}/api`
    });

    return (
      await this.makeRESTRequest<TSTAPIDataResponse>(
        '/api/brand-analytics/v1/dashboard/top-search-terms/reports',
        payload
      )
    ).data.reportsV2[0];
  }

  private async fetchMetadata<T = RawReportMetadataResponse>(
    marketplace: SellerCentralMarketplace,
    baseRefererUrl: string,
    requestUrl: string
  ) {
    await this.addHeadersOverride({
      csrfToken: await this.getCsrfToken(),
      origin: this.baseUrl,
      referer: `${baseRefererUrl}?${new URLSearchParams({ 'country-id': marketplace })}`,
      urlFilter: `${this.baseUrl}/api`
    });

    return (
      await this.makeRESTRequest<T>(requestUrl, {
        selectedCountries: [marketplace]
      })
    ).data;
  }

  async fetchSQPMetadata(marketplace: SellerCentralMarketplace) {
    const baseRefererUrl = `${this.baseUrl}/brand-analytics/dashboard/query-performance`;
    const requestUrl = '/api/brand-analytics/v1/dashboard/query-performance/metadata';
    return this.fetchMetadata(marketplace, baseRefererUrl, requestUrl);
  }

  async fetchSCPMetadata(marketplace: SellerCentralMarketplace) {
    const baseRefererUrl = `${this.baseUrl}/brand-analytics/dashboard/brand-catalog-performance`;
    const requestUrl = '/api/brand-analytics/v1/dashboard/brand-catalog-performance/metadata';
    return this.fetchMetadata(marketplace, baseRefererUrl, requestUrl);
  }

  async fetchTSTMetadata(marketplace: SellerCentralMarketplace) {
    const baseRefererUrl = `${this.baseUrl}/brand-analytics/dashboard/top-search-terms`;
    const requestUrl = '/api/brand-analytics/v1/dashboard/top-search-terms/metadata';
    return this.fetchMetadata(marketplace, baseRefererUrl, requestUrl);
  }

  async fetchSalesAndTrafficReportByASIN(
    amazonDateRange: AmazonDateRange,
    requestType: BusinessReportsRequestType,
    asins: Array<string>,
    page: number
  ) {
    await this.addHeadersOverride({
      origin: this.baseUrl,
      referer: `${this.baseUrl}/business-reports/ref=xx_sitemetric_favb_xx`,
      urlFilter: `${this.baseUrl}/business-reports/api`
    });

    return (
      await this.makeGraphQLRequest<SalesAndTrafficReportResponse>(
        '/business-reports/api',
        SalesAndTrafficRequestUtils.getSalesAndTrafficByASINReportDataQuery(amazonDateRange, requestType, asins, page)
      )
    ).data;
  }

  async fetchSalesAndTrafficReportColumns(requestType: BusinessReportsRequestType) {
    await this.addHeadersOverride({
      origin: this.baseUrl,
      referer: `${this.baseUrl}/business-reports/ref=xx_sitemetric_favb_xx`,
      urlFilter: `${this.baseUrl}/business-reports/api`
    });

    return (
      await this.makeGraphQLRequest<SalesAndTrafficReportResponse>(
        '/business-reports/api',
        SalesAndTrafficRequestUtils.getReportColumnsQuery(requestType)
      )
    ).data;
  }

  async fetchSalesAndTrafficReport(amazonDateRange: AmazonDateRange) {
    await this.addHeadersOverride({
      origin: this.baseUrl,
      referer: `${this.baseUrl}/business-reports/ref=xx_sitemetric_favb_xx`,
      urlFilter: `${this.baseUrl}/business-reports/api`
    });

    return (
      await this.makeGraphQLRequest<SalesAndTrafficReportResponse>(
        '/business-reports/api',
        SalesAndTrafficRequestUtils.getSalesAndTrafficReportDataQuery(amazonDateRange)
      )
    ).data;
  }

  async fetchSellerMetadata() {
    await this.addHeadersOverride({
      origin: this.baseUrl,
      referer: `${this.baseUrl}/business-reports/ref=xx_sitemetric_favb_xx`,
      urlFilter: `${this.baseUrl}/business-reports/api`
    });

    return (
      await this.makeGraphQLRequest<BusinessReportsMetadataResponse>(
        '/business-reports/api',
        SalesAndTrafficRequestUtils.SELLER_METADATA_REQUEST_BODY
      )
    ).data;
  }
}
