import { relations } from 'drizzle-orm';

import {
  businessReportsColumnSelections,
  googleDriveConnections,
  googleDriveDataConfigurations,
  users,
  userSettings
} from './schema';

export const userRelations = relations(users, ({ many, one }) => ({
  googleDriveConnection: one(googleDriveConnections),
  settings: one(userSettings),
  googleDriveDataConfigurations: many(googleDriveDataConfigurations),
  businessReportsColumnSelections: many(businessReportsColumnSelections)
}));

export const googleDriveDataConfigurationRelations = relations(googleDriveDataConfigurations, ({ one }) => ({
  user: one(users, {
    fields: [googleDriveDataConfigurations.userId],
    references: [users.id]
  }),
  googleDriveConnection: one(googleDriveConnections, {
    fields: [googleDriveDataConfigurations.googleDriveConnectionId],
    references: [googleDriveConnections.id]
  })
}));

export const googleDriveConnectionRelations = relations(googleDriveConnections, ({ one, many }) => ({
  user: one(users, {
    fields: [googleDriveConnections.userId],
    references: [users.id]
  }),
  dataConfigurations: many(googleDriveDataConfigurations)
}));

export const businessReportsColumnSelectionRelations = relations(businessReportsColumnSelections, ({ one }) => ({
  user: one(users, {
    fields: [businessReportsColumnSelections.userId],
    references: [users.id]
  })
}));

export const userSettingsRelations = relations(userSettings, ({ one }) => ({
  user: one(users, {
    fields: [userSettings.userId],
    references: [users.id]
  })
}));
