import gqlmin from 'gqlmin';
import type { Variables } from 'graphql-request';

export default class GraphQLHelper {
  static createRequestBody = (
    query: string | Array<string>,
    variables?: Variables | Array<Variables>,
    operationName?: string
  ): string => {
    if (!Array.isArray(query)) {
      return JSON.stringify({ operationName, query: gqlmin(query), variables });
    }

    if (typeof variables !== `undefined` && !Array.isArray(variables)) {
      throw new Error(`Cannot create request body with given variable type, array expected`);
    }

    // Batch support
    const payload = query.reduce<Array<{ query: string; variables: Variables | undefined }>>(
      (acc, currentQuery, index) => {
        acc.push({ query: gqlmin(currentQuery), variables: variables ? variables[index] : undefined });
        return acc;
      },
      []
    );

    return JSON.stringify(payload);
  };
}
