import type { AxiosError, AxiosInstance, AxiosResponse, CreateAxiosDefaults } from 'axios';
import axios from 'axios';
import type { IAxiosRetryConfig } from 'axios-retry';
import axiosRetry, { exponentialDelay } from 'axios-retry';

import { ApiError, ErrorResponse } from './ErrorResponse';

export const FALLBACK_ERROR_MESSAGE = 'Something went wrong';

export const axiosErrorMiddleware = (axiosInstance: AxiosInstance) => {
  axiosInstance.interceptors.response.use(
    (response: AxiosResponse): Promise<AxiosResponse> => Promise.resolve(response),
    (error: AxiosError<{ message?: string }>): Promise<never> => {
      const errorMessage = error.response?.data?.message ?? (error.message || FALLBACK_ERROR_MESSAGE);

      if (error.response && error.status !== ApiError.ServerError) {
        const { data, headers, status } = error.response;

        if (status === ApiError.ServerError) {
          return Promise.reject(
            new ErrorResponse(ApiError.ServerError, {
              headers,
              message: errorMessage,
              rawCode: error.code,
              rawData: data
            })
          );
        }

        let message: string;
        if (headers?.['x-vercel-error'] === 'FUNCTION_INVOCATION_TIMEOUT') {
          message = 'Request timed out';
        } else {
          message = errorMessage;
        }

        return Promise.reject(
          new ErrorResponse(status, {
            headers,
            message,
            rawCode: error.code,
            rawData: data
          })
        );
      }

      return Promise.reject(
        new ErrorResponse(ApiError.ServerError, {
          message: errorMessage,
          rawCode: error.code
        })
      );
    }
  );
};

const createAxiosInstance = (
  withRetries: boolean,
  config?: { axiosConfig?: CreateAxiosDefaults; axiosRetryConfig?: IAxiosRetryConfig }
) => {
  const axiosInstance = axios.create(config?.axiosConfig);

  if (withRetries) {
    axiosRetry(axiosInstance, {
      retries: 5,
      retryDelay: exponentialDelay,
      ...config?.axiosRetryConfig
    });
  }

  // Apply middlewares
  axiosErrorMiddleware(axiosInstance);

  return axiosInstance;
};

export default createAxiosInstance;

export type { AxiosProgressEvent, AxiosResponse } from 'axios';
