import type { AxiosResponse } from '@riptech/utils/src/errors/axiosUtil';
import createAxiosInstance from '@riptech/utils/src/errors/axiosUtil';
import type { ErrorResponse } from '@riptech/utils/src/errors/ErrorResponse';
import { ApiError } from '@riptech/utils/src/errors/ErrorResponse';
import { HTTPErrors } from '@riptech/utils/src/errors/HTTPErrors';
import _ from 'lodash';

export default abstract class BaseRequest {
  private readonly RULE_ID = parseInt(_.uniqueId(), 10);

  baseUrl: string;

  private readonly csrfTokenUrl: string;

  protected csrfToken = '';

  private readonly axios: ReturnType<typeof createAxiosInstance>;

  private async scrapeCsrfToken() {
    const tab = await chrome.tabs.create({ active: false, url: this.csrfTokenUrl });
    if (!tab.id) {
      throw new HTTPErrors.ServerError({
        message: `Failed to open tab for ${this.csrfTokenUrl}`
      });
    }
    const injectionResults = await chrome.scripting.executeScript({
      func: () => {
        const csrfEl = document.querySelector('meta[name="anti-csrftoken-a2z"]');
        return csrfEl?.attributes?.getNamedItem('content')?.value;
      },
      target: { tabId: tab.id }
    });

    await chrome.tabs.remove(tab.id);

    const csrfToken = injectionResults[0].result;
    if (!csrfToken) {
      throw new Error('Failed to get CSRF Token');
    }
    return csrfToken;
  }

  protected async addHeadersOverride({
    csrfToken,
    urlFilter,
    referer,
    origin
  }: {
    referer: string;
    origin: string;
    urlFilter: string;
    csrfToken?: string;
  }) {
    await chrome.declarativeNetRequest.updateDynamicRules({
      addRules: [
        {
          action: {
            requestHeaders: [
              ...(csrfToken
                ? [
                    {
                      header: 'Anti-Csrftoken-A2z',
                      operation: chrome.declarativeNetRequest.HeaderOperation.SET,
                      value: csrfToken
                    }
                  ]
                : []),
              {
                header: 'Origin',
                operation: chrome.declarativeNetRequest.HeaderOperation.SET,
                value: origin
              },
              {
                header: 'Referer',
                operation: chrome.declarativeNetRequest.HeaderOperation.SET,
                value: referer
              },
              {
                header: 'Sec-Fetch-Site',
                operation: chrome.declarativeNetRequest.HeaderOperation.SET,
                value: 'same-origin'
              }
            ],
            type: chrome.declarativeNetRequest.RuleActionType.MODIFY_HEADERS
          },
          condition: {
            initiatorDomains: [chrome.runtime.id],
            resourceTypes: Object.values(chrome.declarativeNetRequest.ResourceType),
            urlFilter
          },
          id: this.RULE_ID,
          priority: 1
        }
      ],
      removeRuleIds: [this.RULE_ID]
    });
  }

  private handleCommonErrors(error: ErrorResponse) {
    if (error?.statusCode === ApiError.Forbidden) {
      return new HTTPErrors.Forbidden({ message: `Please sign in to Amazon Seller Central ${this.baseUrl}` });
    }
    return error;
  }

  private async removeHeadersOverride() {
    await chrome.declarativeNetRequest.updateDynamicRules({
      removeRuleIds: [this.RULE_ID]
    });
  }

  protected constructor(baseUrl: string, csrfTokenUrl: string) {
    this.csrfTokenUrl = csrfTokenUrl;
    this.baseUrl = baseUrl;
    this.axios = createAxiosInstance(
      // Don't retry since errors shouldn't happen and will slow down the user experience
      false,
      {
        axiosConfig: {
          adapter: 'fetch',
          baseURL: this.baseUrl,
          fetchOptions: {
            redirect: 'manual'
          },
          headers: {
            Accept: '*/*',
            'Content-Type': 'application/json'
          }
        }
      }
    );
  }

  protected async getCsrfToken() {
    if (this.csrfToken) {
      return this.csrfToken;
    }
    this.csrfToken = await this.scrapeCsrfToken();
    return this.csrfToken;
  }

  private static handleGraphQLLoginRedirect<T>(response: AxiosResponse<T>) {
    if (!response?.status) {
      throw new HTTPErrors.Forbidden();
    }
    return response;
  }

  private async makeAPIRequest<T = any>(requestType: 'rest' | 'gql', url: string, payload: any) {
    try {
      const response = await this.axios.post<T>(url, payload);
      return requestType === 'gql' ? BaseRequest.handleGraphQLLoginRedirect<T>(response) : response;
    } catch (e: any) {
      throw this.handleCommonErrors(e);
    } finally {
      await this.removeHeadersOverride();
    }
  }

  protected async makeRESTRequest<T = any>(url: string, payload: any) {
    return this.makeAPIRequest<T>('rest', url, payload);
  }

  protected async makeGraphQLRequest<T = any>(url: string, payload: any) {
    return this.makeAPIRequest<T>('gql', url, payload);
  }
}
