import { drizzle } from 'drizzle-orm/vercel-postgres';

import * as relations from './schema/relations';
import * as schema from './schema/schema';

const drizzleClient = () => drizzle({ schema: { ...schema, ...relations } });

declare global {
  // eslint-disable-next-line vars-on-top, no-var
  var localDrizzle: ReturnType<typeof drizzleClient>;
}

const getDrizzleClient = (): ReturnType<typeof drizzleClient> => {
  if (process.env.SERVER) {
    return drizzleClient();
  }

  if (!global.localDrizzle) {
    global.localDrizzle = drizzleClient();
  }
  return global.localDrizzle;
};

// TODO: Remove the cast once https://github.com/drizzle-team/drizzle-orm/issues/3175 is resolved
const db = getDrizzleClient();
export default db as Omit<typeof db, '$client'>;
