import createAxiosInstance from '@riptech/utils/src/errors/axiosUtil';
import { ApiError } from '@riptech/utils/src/errors/ErrorResponse';
import type { MarketplaceType } from '@riptech/utils/src/MarketplaceUtils';
import { MarketplaceUtils } from '@riptech/utils/src/MarketplaceUtils';

import SellerCentralRequestManager from '../utils/request/SellerCentralRequest/SellerCentralRequestManager';
import type {
  FetchSalesAndTrafficReportMessage,
  FetchUserDataMessage,
  InvalidateUserDataCacheMessage
} from '../utils/RequestProxy';
import RequestProxy from '../utils/RequestProxy';
import { API_VERSION_HEADER_KEY, CURRENT_API_VERSION } from '../utils/versioning';

const axios = createAxiosInstance(false, {
  axiosConfig: {
    headers: { [API_VERSION_HEADER_KEY]: CURRENT_API_VERSION }
  }
});

// eslint-disable-next-line no-alert
chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: true }).catch((error) => alert(error));

const CACHE_NAME = 'user-data-cache';

const getUserData = async (cacheKey: string) => {
  const cache = await caches.open(CACHE_NAME);
  const cachedResponse = await cache.match(cacheKey);
  if (cachedResponse) {
    return cachedResponse.json();
  }

  const { data } = await axios.get(cacheKey, { withCredentials: true });
  await cache.put(cacheKey, new Response(JSON.stringify(data)));

  return data;
};

chrome.runtime.onMessageExternal.addListener(
  async (request: FetchSalesAndTrafficReportMessage | FetchUserDataMessage, sender, sendResponse) => {
    switch (request.type) {
      case 'fetchUserData': {
        try {
          const cacheKey = (request as FetchUserDataMessage).url;
          const data = await getUserData(cacheKey);
          sendResponse({ payload: data, type: 'success' });
        } catch (e: any) {
          sendResponse({
            payload: {
              message:
                e.statusCode !== ApiError.Unauthorized && e.message
                  ? e.message
                  : 'Unable to fetch settings. Please ensure you are signed in to Seller Utilities.',
              statusCode: e.statusCode || ApiError.ServerError
            },
            type: 'error'
          });
        }
        break;
      }
      case 'fetchSalesAndTrafficReport': {
        try {
          // verify that the marketplace belongs to the same user & regions are the same
          if ((request as FetchSalesAndTrafficReportMessage).amazonAppConfigUserInfo) {
            const sellerMetadata = await SellerCentralRequestManager.fetchSellerMetadata(
              (request as FetchSalesAndTrafficReportMessage).marketplace
            );
            const sellerId = sellerMetadata?.data?.getSellerMetaData?.sellerId;
            const marketplaceId = sellerMetadata?.data?.getSellerMetaData?.marketplaceId;
            const { amazonAppConfigUserInfo } = request as FetchSalesAndTrafficReportMessage;
            if (
              amazonAppConfigUserInfo?.customerId !== sellerId ||
              amazonAppConfigUserInfo?.marketplaceId !== marketplaceId
            ) {
              const sellerCentralUrl = await SellerCentralRequestManager.getBaseUrl(
                (request as FetchSalesAndTrafficReportMessage).marketplace
              );
              sendResponse({
                payload: {
                  message: `You have an active advertising session for user ${amazonAppConfigUserInfo?.customerId} for the ${MarketplaceUtils.getMarketplaceName(amazonAppConfigUserInfo?.marketplaceId as MarketplaceType) || amazonAppConfigUserInfo?.marketplaceId} marketplace and an active Seller Central session for user ${sellerId} for the ${MarketplaceUtils.getMarketplaceName(marketplaceId as MarketplaceType) || marketplaceId} marketplace. To view this chart, make sure you login to the same account and marketplace for Advertising and Seller Central. Login here: ${sellerCentralUrl}`,
                  statusCode: ApiError.ServerError
                },
                type: 'error'
              });
              break;
            }
          }
          const data = await SellerCentralRequestManager.fetchSalesAndTrafficReport(
            (request as FetchSalesAndTrafficReportMessage).marketplace,
            (request as FetchSalesAndTrafficReportMessage).amazonDateRange
          );
          sendResponse({ payload: data, type: 'success' });
        } catch (e: any) {
          sendResponse({
            payload: {
              message: e.message || 'Unknown error',
              statusCode: e.statusCode || ApiError.ServerError
            },
            type: 'error'
          });
        }
        break;
      }
      default:
        sendResponse({
          payload: 'Unknown Request',
          type: 'error'
        });
    }
  }
);

chrome.runtime.onMessage.addListener((request: InvalidateUserDataCacheMessage, sender, sendResponse) => {
  const handleMessages = async () => {
    switch (request.type) {
      case 'invalidateUserDataCache': {
        const cache = await caches.open(CACHE_NAME);
        await cache.delete(request.url);
        sendResponse();
        break;
      }
      case 'getData': {
        try {
          const data = await getUserData(RequestProxy.USER_DATA_URL);
          sendResponse({ status: 'success', data });
        } catch (error) {
          sendResponse({ status: 'error', message: 'An error occurred' });
        }
        break;
      }
      default:
        sendResponse();
    }
  };
  // eslint-disable-next-line @typescript-eslint/no-floating-promises
  handleMessages();
  return true;
});

chrome.runtime.onInstalled.addListener((details) => {
  if (details.reason === 'install') {
    chrome.runtime?.setUninstallURL('https://forms.gle/8SWoWwWsGv4iaNQN6');
  }
});
