import { HTTPErrors } from '@riptech/utils/src/errors/HTTPErrors';
import type { NextApiRequest } from 'next';
import type { JWT } from 'next-auth/jwt';

import { MINIMUM_VERSION } from './versions';

export const API_VERSION_HEADER_KEY = 'x-api-version';

export const VERSION_LOW_MESSAGE = 'Your Chrome extension is out of date. Please update to the latest version.';

/**
 * Compares user version to the minimum required version to determine if the user version is valid.
 *
 * @param userVersion The user's version string in the format "x.y.z".
 * @returns A boolean indicating whether the user version is lesser than the minimum valid version.
 * @throws An error if the user version string is invalid (i.e., different lengths).
 */
const isUserVersionValid = (userVersion: string) => {
  const userVersionParts = userVersion.split('.').map(Number);
  const minVersionParts = MINIMUM_VERSION.split('.').map(Number);

  if (userVersionParts.length !== minVersionParts.length) {
    throw new Error('Incompatible version types');
  }

  for (let i = 0; i < minVersionParts.length; i++) {
    if (userVersionParts[i] < minVersionParts[i]) {
      return true; // user version is below minimum version
    }
    if (userVersionParts[i] > minVersionParts[i]) {
      return false; // user version is greater than minimum version
    }
  }

  return false; // user version and minimum version are equal
};

export const apiVersionMiddleware = async (req: NextApiRequest<JWT | null>) => {
  const version = req.headers[API_VERSION_HEADER_KEY];

  if (!version || isUserVersionValid(version as string)) {
    throw new HTTPErrors.BadRequest({ message: VERSION_LOW_MESSAGE });
  }
};
