import type { SCPPayloadParameter } from '../request/SellerCentralRequest/utils/SCPRequestUtils';
import type { SQPPayloadParameter } from '../request/SellerCentralRequest/utils/SQPRequestUtils';
import type { TSTPayloadParameter } from '../request/SellerCentralRequest/utils/TSTRequestUtils';

export default class ReportRequestUtils {
  static generateReportRefererUrl = (
    url: string,
    payload: SQPPayloadParameter | SCPPayloadParameter | TSTPayloadParameter,
    viewId: string
  ) => {
    const queryKeyValue = Object.fromEntries(
      payload.filterSelections.filter((option) => option.value).map((option) => [option.id, option.value])
    );

    return `${url}?${new URLSearchParams({
      // Order here is important
      'view-id': viewId,
      ...queryKeyValue,
      'country-id': payload.selectedCountries[0]
    })}`;
  };
}
