// Timezones
export const NA_TIMEZONE = 'America/Los_Angeles';
export const MX_TIMEZONE = 'America/Mexico_City';
export const EST_TIMEZONE = 'America/New_York';
export type Timezone = typeof NA_TIMEZONE | typeof MX_TIMEZONE | typeof EST_TIMEZONE;

export type MarketplaceCountryCode = 'US' | 'CA' | 'MX';

export type CurrencyType = 'USD' | 'CAD' | 'MXN';

const amzUSMarketplace = 'ATVPDKIKX0DER';
const amzCAMarketplace = 'A2EUQ1WTGCTBG2';
const amzMXMarketplace = 'A1AM78C64UM0Y8';

export type MarketplaceType =
  | typeof amzCAMarketplace
  | typeof amzMXMarketplace
  | typeof amzUSMarketplace
  | 'Etsy'
  | 'Shopify'
  | 'WALMART_US'
  | 'WALMART_CA'
  | 'WALMART_MX';
export const AMZ_US_MARKETPLACE: MarketplaceType = amzUSMarketplace;
export const AMZ_CA_MARKETPLACE: MarketplaceType = amzCAMarketplace;
export const AMZ_MX_MARKETPLACE: MarketplaceType = amzMXMarketplace;

export type MarketplaceURL =
  | 'Amazon.com.mx'
  | 'Amazon.com'
  | 'Amazon.ca'
  | 'Etsy.com'
  | 'Shopify.com'
  | 'Walmart.com'
  | 'Walmart.ca'
  | 'Walmart.com.mx';

type MarketplaceIntegrationType = 'Amazon' | 'Etsy' | 'Shopify' | 'Walmart';

export const AMAZON_MARKETPLACES: Array<MarketplaceType> = [AMZ_US_MARKETPLACE, AMZ_CA_MARKETPLACE, AMZ_MX_MARKETPLACE];
export const WALMART_MARKETPLACES: Array<MarketplaceType> = ['WALMART_US', 'WALMART_CA', 'WALMART_MX'];

interface PreviewProps {
  isDraftListing?: boolean;
  handle: string;
  shopifyPreviewUrl?: string | null;
}

export class MarketplaceUtils {
  static INVALID_IMAGE_URLS = [
    'https://images-na.ssl-images-amazon.com/images/I/01RmK+J4pJL._SL75_.gif',
    'http://g-ecx.images-amazon.com/images/G/01/x-site/icons/no-img-sm._CB1535416344_.gif'
  ];

  static getTimezone = (marketplace: MarketplaceType): Timezone => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
      case 'WALMART_MX':
        return MX_TIMEZONE;
      case AMZ_US_MARKETPLACE:
      case AMZ_CA_MARKETPLACE:
      case 'WALMART_US':
      case 'WALMART_CA':
        return NA_TIMEZONE;
      case 'Shopify':
      case 'Etsy':
        return EST_TIMEZONE;
      default:
        return NA_TIMEZONE;
    }
  };

  static getCountryCodeFromMarketplace = (marketplace: MarketplaceType): MarketplaceCountryCode => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
      case 'WALMART_MX':
        return 'MX';
      case AMZ_US_MARKETPLACE:
      case 'Etsy':
      case 'Shopify':
      case 'WALMART_US':
        return 'US';
      case AMZ_CA_MARKETPLACE:
      case 'WALMART_CA':
        return 'CA';
      default:
        throw new Error(`Unknown marketplace encountered: ${marketplace}`);
    }
  };

  static getShortURL = (marketplace: MarketplaceType): MarketplaceURL => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
        return 'Amazon.com.mx';
      case AMZ_US_MARKETPLACE:
        return 'Amazon.com';
      case AMZ_CA_MARKETPLACE:
        return 'Amazon.ca';
      case 'Etsy':
        return 'Etsy.com';
      case 'Shopify':
        return 'Shopify.com';
      case 'WALMART_US':
        return 'Walmart.com';
      case 'WALMART_CA':
        return 'Walmart.ca';
      case 'WALMART_MX':
        return 'Walmart.com.mx';
      default:
        throw new Error(`Unknown marketplace encountered: ${marketplace}`);
    }
  };

  static getMarketplaceFromURL = (marketplace: MarketplaceURL): MarketplaceType => {
    switch (marketplace) {
      case 'Amazon.com.mx':
        return AMZ_MX_MARKETPLACE;
      case 'Amazon.com':
        return AMZ_US_MARKETPLACE;
      case 'Amazon.ca':
        return AMZ_CA_MARKETPLACE;
      case 'Etsy.com':
        return 'Etsy';
      case 'Shopify.com':
        return 'Shopify';
      case 'Walmart.com':
        return 'WALMART_US';
      case 'Walmart.ca':
        return 'WALMART_CA';
      case 'Walmart.com.mx':
        return 'WALMART_MX';
      default:
        return AMZ_US_MARKETPLACE;
    }
  };

  static getMarketplaceName = (marketplace: MarketplaceType): string => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
        return 'Amazon MX';
      case AMZ_US_MARKETPLACE:
        return 'Amazon US';
      case AMZ_CA_MARKETPLACE:
        return 'Amazon CA';
      case 'Etsy':
        return 'Etsy';
      case 'Shopify':
        return 'Shopify';
      case 'WALMART_US':
        return 'Walmart US';
      case 'WALMART_CA':
        return 'Walmart CA';
      case 'WALMART_MX':
        return 'Walmart MX';
      default:
        return '';
    }
  };

  static getIntegrationType = (marketplace: MarketplaceType): MarketplaceIntegrationType => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
      case AMZ_US_MARKETPLACE:
      case AMZ_CA_MARKETPLACE:
        return 'Amazon';
      case 'Etsy':
        return 'Etsy';
      case 'Shopify':
        return 'Shopify';
      case 'WALMART_US':
      case 'WALMART_CA':
      case 'WALMART_MX':
        return 'Walmart';
      default:
        throw new Error(`Unknown marketplace encountered: ${marketplace}`);
    }
  };

  static getCurrencyCode = (marketplace: MarketplaceType): CurrencyType => {
    switch (marketplace) {
      case AMZ_MX_MARKETPLACE:
      case 'WALMART_MX':
        return 'MXN';
      case AMZ_US_MARKETPLACE:
      case 'Etsy':
      case 'Shopify':
      case 'WALMART_US':
        return 'USD';
      case AMZ_CA_MARKETPLACE:
      case 'WALMART_CA':
        return 'CAD';
      default:
        return 'USD';
    }
  };

  static getListingPreviewUrl(integrationType: MarketplaceIntegrationType, preview: PreviewProps): string | null {
    switch (integrationType) {
      case 'Etsy':
        return preview.isDraftListing ? null : `https://www.etsy.com/listing/${preview.handle}`;
      case 'Shopify': {
        return preview.shopifyPreviewUrl ?? null;
      }
      default:
        return null;
    }
  }

  static getEditListingLink(
    listingId: string,
    integrationType: MarketplaceIntegrationType,
    shopifyShopName?: string
  ): string | null {
    switch (integrationType) {
      case 'Etsy':
        return `https://www.etsy.com/your/shops/me/listing-editor/edit/${listingId}`;
      case 'Shopify':
        return `https://${shopifyShopName}/admin/products/${listingId}`;
      default:
        return null;
    }
  }
}
